export default {
  props: {
    placeholder: {
      type: String,
      default: ''
    },
    value: null,
    target: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      query: ''
    }
  },
  methods: {
    focusSearch () {
      this.$emit('focus')
    },
    blurSearch () {
      this.$emit('blur')
    },
    clearQuery () {
      this.query = ''
    },
    setQuery (query) {
      this.query = query
    },
    blur () {
      this.$refs.query.blur()
    }
  },
  watch: {
    query (newQuery) {
      if (this.timer) {
        clearTimeout(this.timer)
      }

      this.timer = setTimeout(() => {
        if (this.target !== '') {
          this.$emit('change', newQuery, this.target)
        } else {
          this.$emit('change', newQuery)
        }
      }, 400)
    }
  }
}



